import styled from "styled-components";
import SectionCard from './SectionCard';
import melody from './img/melody.png';
import quantize from './img/quantize.png';
import instrument from './img/instrument.png';
import copy from './img/copy.png';

const Manualitems = [
    {
        title: `1．メロディを入力する`,
        detail: `メロディを入力するのは簡単！鳴らしたい音を選んで音の長さを決めるだけ。！`,
        image: melody,
    },
    {
        title: `2.クオンタイズを変更する`,
        detail: `設定するときの音の長さを変更することが出来る！`,
        image: quantize,
    },
    {
        title: `3.音色を変える`,
        detail: `ヴァイオリンやフルートなど9種の楽器の音色に変更可能！`,
        image: instrument,
    },
    {
        title: `4.フレーズをコピーする`,
        detail: `気に入ったフレーズをコピーすることで作業効率UP!`,
        image: copy,
    },
    {
        title: ``,
        detail: ``,
        image: ``,
    },
    {
        title: `FFF`,
        detail: ``,
        image: ``,
    },
];

const Manual = (): JSX.Element => {

    const manualJSXList: JSX.Element[] = [];
    Manualitems.forEach((record, i) => {
        manualJSXList.push(
            <SectionCard
                image={record.image}
                title={record.title}
                detail={record.detail}
            />
        )
    });

    return (<>
        <_Record>
            <_Title>マニュアル</_Title>
            <_Cardlist >
                {manualJSXList}
            </_Cardlist>
        </_Record>
    </>
    );
};

const _Record = styled.div`
    display: inline-block;
    width: 100%;
    height: 100%;
    margin-top: 20px;
    background-Color: rgb(141, 209, 86);
`;

const _Title = styled.div`
    font-size: 38px;
    color: rgba(15, 7, 7, 0.719);
    font-weight: 500;
    margin-bottom: 20px;
`;

const _Cardlist = styled.div`
    display: inline-block;
    width: 1000px;
    height: 100%;
    margin-right: auto;
    margin-left: auto;
`;

export default Manual;
